<template>
    <div>
        <center>
            <h1 class="py-4">Liste des courses</h1>
            <router-link to="/new">
                <el-button class="my-3" type="info" plain>
                    Ajouter une liste
                </el-button>
            </router-link>
        </center>
        <div v-for="(item, index) in listes" :key="index" class="row-liste">
            <Liste :item="item" />
        </div>
    </div>
</template>

<script>
    import Liste from './Liste.vue'
    import { mapGetters } from 'vuex'
    export default {
        name: 'Listing',
        components: {
            Liste
        },
        computed: {
            ...mapGetters([
                'listes'
            ])
        },
        methods: {
            openList(listId) {
                this.$router.push({
                    name: 'list',
                    params: {
                        id: listId
                    }
                })
            },
            removeFromList(id) {
                this.$store.commit('REMOVE_LIST', id)
            }
        }
    }
</script>